.test-creation-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  border-radius: 8px;
  height: calc(100vh - 20px);
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;
}
/* General */
.input-group {
  margin-bottom: 15px;
  padding: 0 10px;
}

.input-label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  color: #333;
  padding-left: 5px;
}

.input-field,
.textarea-field,
.question-input,
.option-input {
  font-size: 0.875rem;
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease-in-out;
  box-sizing: border-box;
}

.input-field:hover,
.textarea-field:hover,
.question-input:hover,
.option-input:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-color: #93c5fd;
}

.input-field:focus,
.textarea-field:focus,
.question-input:focus,
.option-input:focus {
  outline: none;
  border-color: #93c5fd;
  transform: translateY(-0.25rem);
}

.input-error,
.textarea-error {
  border-color: red;
}

.char-count {
  display: block;
  font-size: 14px;
  color: #666;
  margin-top: 5px;
  text-align: right;
}

.char-count-error {
  color: red;
}

.textarea-field {
  resize: vertical;
}
/* Basic Info */

/* Survey Types */
.custom-option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  cursor: pointer;
  width: 100%; /* Đảm bảo chiếm toàn bộ chiều ngang */
  box-sizing: border-box;
  min-width: 0; /* Ngăn chặn tràn nội dung */
}

.custom-option span {
  flex: 1; /* Cho phép span mở rộng để chiếm không gian còn lại */
  margin-right: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.action-buttons-option {
  display: flex;
  align-items: center;
  display: flex;
  white-space: nowrap;
  gap: 4px;
  flex-shrink: 0; /* Ngăn các nút co lại quá mức */
}

.custom-option .edit-btn,
.custom-option .delete-btn {
  padding: 8px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
}

/* btn */

.add-test-types-btn {
  color: #1890ff;
  font-weight: 500;
}

.edit-btn {
  background-color: #1890ff;
  color: white;
}

.delete-btn {
  background-color: #ff4d4f;
  color: white;
}

.save-btn,
.confirm-btn {
  padding: 8px 16px;
  background-color: #1890ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.cancel-btn {
  padding: 8px 16px;
  background-color: #f5f5f5;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  cursor: pointer;
}

.remove-btn {
  padding: 5px 10px;
  background-color: #ff4d4f;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.remove-btn:disabled {
  background-color: #d9d9d9;
  cursor: not-allowed;
}

.add-answer-btn {
  margin-top: 10px;
  padding: 8px 16px;
  background-color: #52c41a;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.remove-question-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent; /* Không có nền */
  padding: 5px; /* Không có padding */
  margin: 0; /* Không có margin */
  color: #333;
  font-family: inherit;
  line-height: 2.5em;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  transition: color 0.3s;
  z-index: 1;
  font-size: 17px;
  border-radius: 6px;
  font-weight: 500;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}

.remove-question-button:before {
  content: "";
  position: absolute;
  z-index: -1;
  background: red;
  height: 150px;
  width: 200px;
  border-radius: 50%;
  top: 100%;
  left: 100%;
  transition: all 0.7s;
}

.remove-question-button:hover {
  color: #fff;
}

.remove-question-button:hover:before {
  top: -30px;
  left: -30px;
}

.remove-question-button:active:before {
  background: #3a0ca3;
  transition: background 0s;
}

.add-question-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent; 
  border: none; 
  cursor: pointer; 
  padding: 0; 
  margin: 0; 
  color: #333;
  background-color: #fff;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  padding: 1.3em 3em;
  border: 1px solid #ddd;
  font-weight: 500;
}

.add-question-button:hover {
  background-color: #23c483;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-7px);
}

.add-question-button:active {
  transform: translateY(-1px);
}

.import-button {
  display: inline-flex;
  align-items: center;
  padding: 8px 16px;
  background-color: #1890ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.import-button:hover {
  background-color: #40a9ff;
}

.submit-button {
  display: flex;
  background-color: blue;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 100px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  transition: all 0.2s;
  align-self: center;
  margin-top: 20px;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.submit-button:hover {
  background-color: blue;
  box-shadow: 0px 10px 20px rgba(0, 0, 255, 0.4);
  transform: translateY(-2px);
}

.submit-button > .IoCreateOutline {
  transform: translateY(-2px);
  font-size: 24px;
  margin-left: 5px;
  transition: transform 0.3s ease-in-out;
}

.submit-button:hover .IoCreateOutline {
  transform: translate(2px, -2px);
}
.submit-button:active {
  transform: scale(1);
}

/* icon */
.remove-icon,
.add-icon-ques,
.add-icon-evaluate,
.remove-icon-ans,
.add-icon-ans {
  background: transparent; /* Đảm bảo không có nền */
  border: none; /* Không có viền xung quanh icon */
  padding: 0; /* Xóa padding */
  margin: 0; /* Xóa margin */
  color: inherit; /* Dùng màu chữ của phần tử chứa */
  font-size: 20px; /* Kích thước của icon */
  cursor: pointer; /* Khi hover lên sẽ có con trỏ */
}
/* Modal */
.custom-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 500px;
  max-width: 90%;
}

.custom-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.custom-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  max-width: 90%;
}

.custom-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.modal-title {
  margin-bottom: 20px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 20px;
}

/* Answer Set */
.answer-set-preview {
  margin-top: 10px;
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 4px;
}

.answer-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 8px;
}

.answer-item {
  padding: 8px;
  background-color: white;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  font-size: 14px;
}

.answers-container {
  margin: 20px 0;
}

.answer-row {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  align-items: center;
}
/* Question */
.question-section {
  display: flex;
  flex-wrap: wrap-reverse;
}

.question-content,
.question-list-container {
  border: 1px solid #ddd;
  background-color: white;
  border-radius: 16px;
  margin-right: 10px;
  padding: 20px;
  margin: 10px;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}

.question-content {
  width: 70%;
}

.question-container {
  margin-bottom: 20px;
}

.question-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.question-label,
.options-label {
  font-weight: bold;
  margin-bottom: 5px;
  padding-left: 5px; /* Thêm padding để tránh dính vào mép */
  text-align: left;
}

.question-type-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px; /* Khoảng cách giữa các câu hỏi */
  width: 100%;
}

.question-type-select {
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%; /* Chiếm hết chiều rộng của container */
  box-sizing: border-box;
  margin-bottom: 10px;
  margin-top: 10px;
  text-align: left;
}

.question-type-select:focus {
  border-color: #007bff; /* Màu border khi focus */
  outline: none;
}

.question-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(50px, 1fr));
  grid-auto-rows: 50px;
  gap: 10px;
}
.question-list-item:hover {
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  transform: translateY(-2px);
}

.question-list-item.selected {
  background-color: #23c483;
  color: white;
  font-weight: bold;
}

.question-list-item {
  color: #333;
  border-radius: 7px;
  border: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.import-section {
  margin: 20px 0;
  display: flex;
  justify-content: center;
}

/* Evaluate */
.evaluate-table {
  margin-top: 10px;
  overflow-x: auto;
}

.evaluate-table table {
  width: 100%;
  border-collapse: collapse;
  min-width: 600px;
}

.evaluate-table th,
.evaluate-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.evaluate-table th {
  background-color: #f4f4f4;
  font-weight: 500;
}

.evaluate-input {
  width: 100%;
  padding: 4px 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.evaluate-input:focus {
  outline: none;
  border-color: #40a9ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}

/* Scroll bar custom */

.test-creation-container::-webkit-scrollbar {
  width: 0px; 
}

.test-creation-container::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

.test-creation-container::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 10px;
}

.test-creation-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.test-creation-container {
  scrollbar-width: none;
}

.answer-input-group {
  display: flex;
  gap: 10px;
  flex: 1;
}

.answer-input {
  flex: 1;
  font-size: 0.875rem;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease-in-out;
}

.score-input {
  width: 80px;
  font-size: 0.875rem;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease-in-out;
}

.answer-input:hover,
.score-input:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.answer-input:focus,
.score-input:focus {
  outline: none;
  border-color: #93c5fd;
}