/* DocumentManagement.css */


/* Cột Khóa */
.documents-table th:nth-child(2), .documents-table td:nth-child(2) {
    width: 100px; /* Điều chỉnh độ rộng của cột "Khóa" */
    text-align: center; /* Căn giữa nội dung trong cột */
}

/* Các phần khác không thay đổi */
.upload-document-container {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.upload-form {
    margin-bottom: 20px;
}

.file-input, .key-input {
    width: 100%;
    padding: 8px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
}

.upload-btn {
    background-color: #4CAF50;
    color: white;
    padding: 10px 15px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 4px;
    width: 100%;
}

.upload-btn:hover {
    background-color: #45a049;
}

.document-list {
    margin-top: 20px;
}

.documents-table-wrapper {
    max-height: 400px;
    overflow-y: auto;
    margin-top: 10px;
}

.documents-table {
    width: 100%;
    border-collapse: collapse;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.documents-table th, .documents-table td {
    padding: 10px;
    text-align: left;
    border: 1px solid #ddd;
}

.documents-table th {
    background-color: #f4f4f4;
    font-weight: bold;
}

.documents-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

.documents-table tr:hover {
    background-color: #f1f1f1;
}
