/* Sidebar container */
.sidebar {
  width: 220px;
  height: 100vh;
  background-color: #1e1e2d;
  color: #f2f4f7;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 2px 0px 15px rgba(0, 0, 0, 0.2);
}

/* Sidebar Header */
.sidebar-header {
  text-align: center;
  padding-bottom: 20px;
  position: relative;
}

.sidebar-header .logo {
  max-width: 60px;
  height: auto;
  border-radius: 50%;
  margin-bottom: 8px;
}

.sidebar-header p {
  font-size: 0.75rem;
  font-weight: 600;
  margin: 4px 0;
  letter-spacing: 0.5px;
  color: #f2f4f7;
}

/* Sidebar Menu */
.sidebar-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-grow: 1;
  overflow-y: auto;
  scrollbar-width: thin; /* Thu hẹp thanh cuộn cho Firefox */
  scrollbar-color: #444 #1E1E2D; /* Màu thanh cuộn (Firefox) */
}
/* Thanh cuộn cho trình duyệt WebKit (Chrome, Edge, Safari) */
.sidebar-menu::-webkit-scrollbar {
  width: 6px; /* Độ rộng của thanh cuộn */
}

.sidebar-menu::-webkit-scrollbar-track {
  background: #1E1E2D; /* Màu nền của thanh cuộn */
}

.sidebar-menu::-webkit-scrollbar-thumb {
  background: #444; /* Màu của thanh cuộn */
  border-radius: 5px; /* Góc bo tròn */
}

.sidebar-menu::-webkit-scrollbar-thumb:hover {
  background: #555; /* Màu thanh cuộn khi hover */
}

.sidebar-menu .menu-section {
  padding: 8px 16px;
  font-size: 0.7rem;
  color: #767373;
  text-transform: uppercase;
  border-bottom: 1px solid #444;
}

.sidebar-menu li {
  padding: 8px 16px;
  transition: background-color 0.3s ease; /* Thêm hiệu ứng background khi hover */
}


.sidebar-menu li a {
  text-decoration: none;
  color: #a1a3a1;
  font-weight: 400;
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  transition: color 0.3s ease, padding-left 0.3s ease;
}

.sidebar-menu li a .icon {
  margin-right: 8px;
  font-size: 0.9rem;
}

/* Hover effect for li */
.sidebar-menu li:hover {
  background-color: #333; 
}

/* Hover effect for links */
.sidebar-menu li a:hover {
  color: white; /* Thay đổi màu chữ khi hover */
  padding-left: 15px; /* Di chuyển liên kết về phía trái */
}

/* Active menu item */
.sidebar-menu li a.active {
  color: white;
  padding-left: 15px;
}

.sidebar-menu li:has(a.active) {
  background-color: #333;
}

/* Sidebar Footer */
.sidebar-footer {
  padding: 8px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  
  border-radius: 5pt;
  margin-top: auto; /* Đảm bảo footer ở dưới cùng */
  flex-shrink: 0; 
}

.sidebar-footer button {
  background: none;
  border: none;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  width: 32px;
  height: 32px;
  transform: translateY(-8px);
}

.sidebar-footer button:hover {
  transform: translateY(-10px);
}

/* Định dạng chung cho các nút trong sidebar-footer */
.sidebar-footer button {
  background: none; /* Loại bỏ nền mặc định */
  border: 2px solid #444; /* Viền xung quanh */
  color: white; /* Màu biểu tượng */
  font-size: 1.2em; /* Kích thước biểu tượng */
  cursor: pointer; /* Con trỏ chuột dạng nhấp */
  border-radius: 50%; /* Tạo hình tròn */
  width: 40px; /* Kích thước nút */
  height: 40px; /* Kích thước nút */
  display: flex; /* Để căn giữa nội dung */
  justify-content: center; /* Căn giữa theo chiều ngang */
  align-items: center; /* Căn giữa theo chiều dọc */
  transition: all 0.3s ease; /* Hiệu ứng mượt mà khi hover */
  transform: translateY(-10px); /* Dịch lên trên nhẹ nhàng */
}

/* Nút Logout */
.sidebar-footer .logout-button:hover {
  background: red; /* Nền đỏ khi hover */
  color: white; /* Màu biểu tượng */
  transform: translateY(-15px); /* Dịch lên trên khi hover */
}

/* Nút Profile */
.sidebar-footer .profile-button:hover {
  background: yellow; /* Nền vàng khi hover */
  color: white; /* Màu biểu tượng */
  transform: translateY(-15px); /* Dịch lên trên khi hover */
}

/* Nút Help */
.sidebar-footer .help-button:hover {
  background: blue; /* Nền xanh khi hover */
  color: white; /* Màu biểu tượng */
  transform: translateY(-15px); /* Dịch lên trên khi hover */
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.icon {
  margin-right: 8px;
}
.modal {
  background: #fff;
  padding: 16px;
  border-radius: 6px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.modal p {
  color: #2d2c2c;
  font-size: 0.875rem;
}

.modal-buttons {
  margin-top: 12px;
}

.confirm-button {
  background-color: red;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  font-size: 0.875rem;
}

.cancel-button {
  background-color: grey;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.875rem;
}

.confirm-button:hover {
  background-color: darkred;
}

.cancel-button:hover {
  background-color: darkgrey;
}