/* CSS cho container chính */
.test-creation-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px; /* Giới hạn độ rộng tối đa của nội dung */
  margin: 0 auto; /* Căn giữa container */
  border-radius: 8px;
  height: calc(
    100vh - 20px
  ); /* Chiếm toàn bộ chiều cao của trang, trừ margin */
  overflow-y: auto; /* Kích hoạt cuộn dọc */
  overflow-x: hidden;
  box-sizing: border-box;
}
/* Nhóm checkbox */
.checkbox-group {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/* Nhãn checkbox */
.checkbox-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  color: #333;
  gap: 12px; /* Khoảng cách giữa checkbox và text */
}

/* Ẩn checkbox mặc định */
.checkbox-input {
  display: none;
}

/* Nhóm toggle switch */
.toggle-switch {
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 12px; /* Khoảng cách giữa switch và nhãn */
  margin-left: 15px;
}

/* Nhãn text */
.toggle-label {
  font-size: 18px;
  font-weight: 500;
  color: #333;
}

/* Ẩn checkbox mặc định */
.switch-input {
  display: none;
}

/* Vỏ ngoài của toggle switch */
.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 28px;
  cursor: pointer;
}

/* Thanh gạt */
.switch-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 28px;
  transition: all 0.3s ease;
}

/* Nút gạt */
.switch-slider::before {
  content: "";
  position: absolute;
  width: 22px;
  height: 22px;
  background-color: white;
  border-radius: 50%;
  top: 3px;
  left: 3px;
  transition: all 0.3s ease;
}

/* Trạng thái đã bật */
.switch-input:checked + .switch-slider {
  background-color: #4caf50; /* Màu nền khi bật */
}

/* Vị trí nút gạt khi bật */
.switch-input:checked + .switch-slider::before {
  transform: translateX(22px); /* Di chuyển nút sang phải */
}

/* Kiểu dáng cho các nhóm input */
.input-group {
  margin-bottom: 15px;
  padding: 0 10px; /* Tạo khoảng cách bên trái và phải */
}

.input-label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  color: #333;
  padding-left: 5px; /* Thêm padding bên trái để tránh dính vào mép */
}

.question-input {
  word-wrap: break-word;
  white-space: pre-wrap;
}
.input-field,
.textarea-field,
.question-input,
.option-input {
  font-size: 0.875rem; /* text-sm */
  width: 100%; /* w-full */
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 7px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05); /* shadow-sm */
  transition: all 0.3s ease-in-out; /* transition duration-300 ease-in-out */
  box-sizing: border-box;
}

.input-field:hover,
.textarea-field:hover,
.question-input:hover,
.option-input:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* hover:shadow-lg */
  border-color: #93c5fd; /* hover:border-blue-300 */
}

.input-field:focus,
.textarea-field:focus,
.question-input:focus,
.option-input:focus {
  outline: none; /* focus:outline-none */
  border-color: #93c5fd; /* focus:outline-blue-300 */
  transform: translateY(-0.25rem); /* focus:-translate-y-1 */
}

/* CSS cho nút "Tạo" */
.submit-button {
  display: flex;
  background-color: blue;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 100px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  transition: all 0.2s;
  align-self: center;
  margin-top: 20px;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.submit-button:hover {
  background-color: blue;
  box-shadow: 0px 10px 20px rgba(0, 0, 255, 0.4);
  transform: translateY(-2px);
}

.submit-button > .IoCreateOutline {
  transform: translateY(-2px);
  font-size: 24px;
  margin-left: 5px;
  transition: transform 0.3s ease-in-out;
}

.submit-button:hover .IoCreateOutline {
  transform: translate(2px, -2px);
}
.submit-button:active {
  transform: scale(1);
}

/* Kiểu dáng cho icon */
.remove-icon,
.add-icon-ques,
.add-icon-evaluate,
.remove-icon-ans,
.add-icon-ans {
  background: transparent; /* Đảm bảo không có nền */
  border: none; /* Không có viền xung quanh icon */
  padding: 0; /* Xóa padding */
  margin: 0; /* Xóa margin */
  color: inherit; /* Dùng màu chữ của phần tử chứa */
  font-size: 20px; /* Kích thước của icon */
  cursor: pointer; /* Khi hover lên sẽ có con trỏ */
}

.add-icon-ques {
  color: #2ecc71; /* Màu xanh lá cho nút thêm */
}
.add-icon-evaluate {
  color: #2ecc71;
}

.remove-icon-ans {
  color: #f39c12; /* Màu cam cho nút xóa đáp án */
}

.add-icon-ans {
  color: #3498db; /* Màu xanh dương cho nút thêm đáp án */
}

/* Thêm hiệu ứng hover cho các icon */
.add-icon-ques:hover,
.remove-icon-ans:hover,
.add-icon-ans:hover {
  opacity: 0.7; /* Giảm độ mờ đi khi hover */
}

/* From Uiverse.io by cssbuttons-io */
.remove-question-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent; /* Không có nền */
  padding: 5px; /* Không có padding */
  margin: 0; /* Không có margin */
  color: #333;
  font-family: inherit;
  line-height: 2.5em;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  transition: color 0.3s;
  z-index: 1;
  font-size: 17px;
  border-radius: 6px;
  font-weight: 500;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}

.remove-question-button:before {
  content: "";
  position: absolute;
  z-index: -1;
  background: red;
  height: 150px;
  width: 200px;
  border-radius: 50%;
  top: 100%;
  left: 100%;
  transition: all 0.7s;
}

.remove-question-button:hover {
  color: #fff;
}

.remove-question-button:hover:before {
  top: -30px;
  left: -30px;
}

.remove-question-button:active:before {
  background: #3a0ca3;
  transition: background 0s;
}

/* Cải thiện độ hiển thị cho nút bấm */
.add-question-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent; /* Không có nền */
  border: none; /* Không có viền */
  cursor: pointer; /* Khi hover sẽ có con trỏ */
  padding: 0; /* Không có padding */
  margin: 0; /* Không có margin */
  color: #333;
  background-color: #fff;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  padding: 1.3em 3em;
  border: 1px solid #ddd;
  font-weight: 500;
}

/* Thêm hiệu ứng hover cho các nút thêm và xóa */
.add-question-button:hover {
  background-color: #23c483;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-7px);
}

.add-question-button:active {
  transform: translateY(-1px);
}

.question-section {
  display: flex;
  flex-wrap: wrap-reverse;
}

.question-content,
.question-list-container {
  border: 1px solid #ddd;
  background-color: white;
  border-radius: 16px;
  margin-right: 10px;
  padding: 20px;
  margin: 10px;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}
.question-content {
  width: 70%;
}
.question-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(50px, 1fr));
  grid-auto-rows: 50px;
  gap: 10px;
}
.question-list-item:hover {
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  transform: translateY(-2px);
}

.question-list-item.selected {
  background-color: #23c483;
  color: white;
  font-weight: bold;
}

.question-list-item {
  color: #333;
  border-radius: 7px;
  border: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

/* Căn chỉnh tiêu đề câu hỏi và nút xóa */
.question-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

/* Kiểu dáng cho các câu hỏi và đáp án */
.question-container {
  margin-bottom: 20px;
}

.question-label,
.options-label {
  font-weight: bold;
  margin-bottom: 5px;
  padding-left: 5px; /* Thêm padding để tránh dính vào mép */
  text-align: left;
}
.score-label {
  font-weight: bold;
  text-align: right;
  flex-shrink: 0;
  margin-top: -23px;
  margin-right: 90px;
}

.options-group {
  display: flex;
  flex-direction: column;
}

.option-input {
  margin-bottom: 5px;
}

/* CSS cho phần hiển thị số ký tự */
.char-count {
  display: block;
  font-size: 14px;
  color: #666;
  margin-top: 5px;
  text-align: right;
}

.char-count-error {
  color: red;
}

.input-error,
.textarea-error {
  border-color: red;
}

.textarea-field {
  resize: vertical;
}

/* Thêm các style cho các nút xóa và thêm đáp án */
.remove-option-button,
.add-option-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.remove-option-button {
  margin-left: 5px;
}

.add-option-button {
  margin-top: 10px;
  color: #3498db;
  font-size: 16px;
}

.add-option-button:hover {
  text-decoration: underline;
}

/* Style cho phần chọn loại câu hỏi */
.question-type-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px; /* Khoảng cách giữa các câu hỏi */
  width: 100%;
}

.question-type-label {
  font-size: 14px;
  font-weight: 600;
  color: #333;
  margin-bottom: 5px; /* Khoảng cách giữa label và select */
  text-align: left;
}

.question-type-select {
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%; /* Chiếm hết chiều rộng của container */
  box-sizing: border-box;
  margin-bottom: 10px;
  margin-top: 10px;
  text-align: left;
}

/* Cải thiện giao diện của menu khi được chọn */
.question-type-select:focus {
  border-color: #007bff; /* Màu border khi focus */
  outline: none;
}

/* Kiểu dáng cho thanh cuộn dọc (Webkit - Chrome, Safari, Edge) */
.test-creation-container::-webkit-scrollbar {
  width: 0px; /* Giảm độ rộng của thanh cuộn dọc */
}

/* Kiểu dáng cho track của thanh cuộn dọc */
.test-creation-container::-webkit-scrollbar-track {
  background: #f1f1f1; /* Màu nền cho track */
  border-radius: 10px;
}

/* Kiểu dáng cho thanh cuộn */
.test-creation-container::-webkit-scrollbar-thumb {
  background: #888; /* Màu thanh cuộn */
  border-radius: 10px;
}

/* Khi hover trên thanh cuộn */
.test-creation-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Firefox */
.test-creation-container {
  scrollbar-width: none;
}

.error-message {
  color: red; /* Màu chữ đỏ */
  font-size: 16px;
  margin-top: 5px;
}

.option-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.option-input {
  flex: 1;
  margin-right: 10px;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.option-score-input {
  width: 60px;
  margin-bottom: 5px;
  margin-right: 10px;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05); /* shadow-sm */
  transition: all 0.3s ease-in-out; /* transition duration-300 ease-in-out */
