.form-container {
    display: flex;
    justify-content: center; /* Canh giữa theo chiều ngang */
    align-items: center; /* Canh giữa theo chiều dọc */
    height: 100vh;
    background-color: #f4f4f4;
  }
  
  .wrapper {
    width: 100%;
    max-width: 400px; /* Giới hạn chiều rộng form */
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .wrapper h2 {
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color: #565353;
  }

  .wrapper p {
    text-align: center;
    font-size: 16px;
    color: #b3acac;
    margin-bottom: 50px;
  }
  
  .logo {
    display: block;
    margin: 0 auto 20px; /* Canh giữa logo và cách phần dưới 20px */
    max-width: 100px; /* Đặt kích thước tối đa của logo */
    height: auto; /* Giữ tỷ lệ của logo */
  }
  
  .form-group {
    margin-bottom: 15px;
    text-align: left;
  }
  
  .form-group label {
    display: block;
    font-size: 14px;
    color: #333;
    margin-bottom: 5px;
  }
  
  .email-form {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 8px; /* Làm tròn các góc */
    box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.8); /* Màu nền mờ */
    transition: all 0.3s ease; /* Hiệu ứng chuyển đổi */
  }

  .email-form:focus {
    border-color: #007bff; /* Đổi màu viền khi focus */
    background-color: rgba(255, 255, 255, 1); /* Nền trong suốt khi focus */
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.5); /* Hiệu ứng bóng mờ */
    outline: none; /* Bỏ viền mặc định */
  }
  
  /* Thêm hiệu ứng cho ô nhập khi hover */
  .email-form:hover {
    box-shadow: 0 0 8px rgba(0, 123, 255, 0.3); /* Bóng mờ khi hover */
  }
  
  
  .submitButton {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submitButton:hover {
    background-color: #0056b3;
  }
  